import React from 'react';
import './filters.css';
import Button from '@material-ui/core/Button';
import { createMuiTheme, ThemeProvider, makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import LazyLoad from 'react-lazy-load';
import Divider from '@material-ui/core/Divider';
import { withStyles } from '@material-ui/core/styles';
import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import CheckBoxOutlineBlankIcon from '@material-ui/icons/CheckBoxOutlineBlank';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import Slider from '@material-ui/core/Slider';

const marks = [
  {
    value: 20,
    label: '₹ 100',
  },
  {
    value: 40,
    label: '₹ 150',
  },
  {
    value: 60,
    label: '₹ 200',
  },
  {
    value: 80,
    label: '₹ 250',
  },
  {
    value: 100,
    label: '₹ 300',
  },
];





const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    '& > *': {
      margin: theme.spacing(3),
      padding: theme.spacing(3),
      width : '100%',
      height: 400,
      float : 'right',
    },
  },
  slider : {
  		 display: 'flex',
    '& > *': {
     	color: '#FF4949',
    },
  }
}));

const GreenCheckbox = withStyles({
  root: {
    color: '#FF4949',
    '&$checked': {
      color: '#FF4949',
    },
  },
  checked: {},
})((props) => <Checkbox color="default" {...props} />);

function FilterView(props){
	const classes = useStyles();

	return(	
			<div className='filterRoot'>
		     <div className={classes.root}>
		     <Paper variant="outlined">
		     <div className='filter'>
		     <div className='filterHead'>
		     <center>Filters</center>
		     </div>
		     <Divider variant="middle" />

		     <div className='filterBody'>


		     <div className='TitleOptions'>
		     <div className='OptionName'>Brands </div>
		     <FormGroup row>
		     
		      <FormControlLabel
        control={<GreenCheckbox name="checkedA" />}
        label="Hero"
      />
       <FormControlLabel
        control={<GreenCheckbox name="checkedB" />}
        label="Honda"
      />
       <FormControlLabel
        control={<GreenCheckbox  name="checkedC" />}
        label="TVS"
      />
       <FormControlLabel
        control={<GreenCheckbox  name="checkedC" />}
        label="Bajaj"
      />
      </FormGroup>

		     </div>
		       <Divider/>
		     <div className='PriceSlider'>
		     <div className='OptionName'> Price Range</div>
		      <Slider
        defaultValue={0}
        aria-labelledby="discrete-slider-custom"
        step={20}
        valueLabelDisplay="auto"
        marks={marks}
        className={classes.slider}
      />
      		</div>
		     </div>


		     </div>
		     </Paper>
		     </div>
		     </div>
		)
}

class Filters extends React.Component {

	constructor(props){
		super(props);
		//this.state
	}


	render(){
			
			return(
					<FilterView/>
					)
 }
}

export default Filters;